.input {
    display: flex;
    position: relative;
    align-items: center;
}

.input__box {
    width: 100%;
    border-radius: 50px;
    padding: 20px 30px;
    font-size: 25px;
    border: none;
    transition: 0.2s;
    box-shadow: inset 0 0 5px black;
}

.input__submit {
    position: absolute;
    width: 50px;
    height: 50px;
    margin: 12px;
    font-size: 12px;
    border-radius: 50px;
    right: 0px;
    border: none;
    background-color: #2f75c0;
    color: white;
    transition: 0.2s all;
    box-shadow: 0 0 10px black;
}

.input__submit:hover {
    background-color: #0d6acd;
}

.input_submit:active {
    transform: scale(0.8);
    box-shadow: 0 0 5px black;
}